<template>
  <div v-if="mounted" class="board" :class="d">
    <div v-for="(col, i) in board.matrix" :key="i" class="column">
      <div
        v-for="(tiles, index) in col"
        v-bind:class="[
          'tile',
          { body: tiles.Body },
          { fruit: tiles.Fruit },
          { head: tiles.Head },
        ]"
        :key="index"
        :style="cssVarsTile()"
      ></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Board",
  data: () => {
    return {
      width: null,
      board: null,
      mounted: false,
      direction: null,
      d: "right",
      up: [-1, 0],
      right: [0, 1],
      down: [1, 0],
      left: [0, -1],
      constant_true: true,
      is_running: false,
      score: 0,
      speed: 150,
    };
  },
  watch: {
    score: function(new_val) {
      this.$emit("change", new_val);
    },
  },
  created: function() {
    window.addEventListener("resize", () => {
      this.Generate_Board();
    });
    window.addEventListener("keydown", (e) => {
      this.Change_Direction(e.keyCode);
    });
  },
  destroyed: function() {
    window.addEventListener("resize", () => {
      this.Generate_Board();
    });
  },
  mounted() {
    this.Generate_Board();
    this.mounted = true;
  },
  methods: {
    cssVarsTile: function() {
      return {
        height: this.width + "px",
        width: this.width + "px",
      };
    },
    Board: function(y, x) {
      this.y = y;
      this.x = x;
      this.matrix = new Array(y);
      for (var i = 0; i < y; i++) {
        this.matrix[i] = new Array(x);
        for (var j = 0; j < x; j++) {
          this.matrix[i][j] = {
            Empty: true,
            Head: false,
            Body: false,
            Friut: false,
          };
        }
      }
      this.Snake = function(y, x) {
        this.head = {
          x: x,
          y: y,
        };
        this.size = 1;
        this.body = [[y, x]];
      };
      var center_x = Math.floor(x / 2);
      var center_y = Math.floor(y / 2);
      this.snake = new this.Snake(center_y, center_x);
      this.h_y = center_y;
      this.h_x = center_x;
      this.matrix[center_y][center_x].Body = true;
      this.matrix[center_y][center_x].Head = true;
    },
    Generate_Board: function() {
      var h = document.querySelector(".v-toolbar").scrollHeight;
      //var width = Math.floor((window.innerWidth - 2 * 20) / size);
      //var height = Math.floor((window.innerHeight - h - 40) / size);
      var width = 25;
      var height = 25;
      var margin_x = 40;
      var margin_y = 20;
      if (window.innerWidth <= 900) {
        margin_x = 0;
      }
      if (innerWidth - margin_x > window.innerHeight - h - margin_y) {
        this.width = Math.floor((window.innerHeight - margin_y - h) / height);
        var strech = Math.floor(
          (window.innerWidth - margin_x) / this.width + 1
        );
        this.board = new this.Board(height, strech);
      } else {
        this.width = Math.floor((window.innerWidth - margin_x) / width);
        this.board = new this.Board(width, width);
      }
    },
    Change_Direction: function(key) {
      switch (key) {
        case 37: //Left
        case 65:
          if (this.direction != this.right) {
            this.direction = this.left;
            this.d = "left";
          }
          break;
        case 38: //Up
        case 87:
          if (this.direction != this.down) {
            this.direction = this.up;
            this.d = "up";
          }
          break;
        case 39: //Right
        case 68:
          if (this.direction != this.left) {
            this.direction = this.right;
            this.d = "right";
          }
          break;
        case 40: //Down
        case 83:
          if (this.direction != this.up) {
            this.direction = this.down;
            this.d = "down";
          }
          break;
      }
    },
    Spawn_Fruit: function() {
      var x = Math.floor(Math.random() * this.board.x);
      var y = Math.floor(Math.random() * this.board.y);
      this.board.matrix[y][x].Fruit = true;
      this.board.matrix[y][x].Empty = false;
    },
    Start: async function() {
      this.Generate_Board();
      this.d = "right";
      this.score = 0;
      var x = this.board.h_x;
      var y = this.board.h_y;
      this.Spawn_Fruit();
      this.direction = this.right;
      while (this.constant_true) {
        var next = {
          x: x + this.direction[1],
          y: y + this.direction[0],
        };
        if (
          next.y == this.board.y ||
          next.y < 0 ||
          next.x == this.board.x ||
          next.x < 0
        ) {
          break;
        }
        if (this.board.matrix[next.y][next.x].Body) {
          break;
        }
        if (this.board.matrix[next.y][next.x].Fruit) {
          this.board.matrix[next.y][next.x].Fruit = false;
          this.board.snake.size++;
          this.score++;
          this.Spawn_Fruit();
        } else {
          var tail = this.board.snake.body.shift();
          this.board.matrix[tail[0]][tail[1]].Head = false;
          this.board.matrix[tail[0]][tail[1]].Body = false;
          this.board.matrix[tail[0]][tail[1]].Empty = true;
        }
        this.board.matrix[y][x].Head = false;
        this.board.matrix[next.y][next.x].Head = true;
        this.board.matrix[next.y][next.x].Body = true;
        this.board.matrix[next.y][next.x].Empty = false;
        this.board.snake.body.push([next.y, next.x]);
        y = y + this.direction[0];
        x = x + this.direction[1];
        await new Promise((y) => setTimeout(y, this.speed));
      }
    },
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/styles/board.scss";
.fruit {
  background-image: url("../assets/media/apple.png");
  background-size: cover;
}
.head {
  background-image: url("../assets/media/head.png");
  background-size: cover;
}
.body:not(.head) {
  background-image: url("../assets/media/tail.png");
  background-size: cover;
}
</style>
