<template>
  <v-app-bar src="../assets/media/nav.jpg">
    <v-toolbar-title>VueSnake</v-toolbar-title>

    <img class="mx-2" src="../assets/media/snake.svg" alt="" />

    <v-spacer></v-spacer>

    <v-btn :disabled="is_locked" v-on:click="start" class="mx-2" color="primary"
      >Start</v-btn
    >

    <v-spacer></v-spacer>

    <v-toolbar-title>Score: {{ score }}</v-toolbar-title>

    <v-spacer></v-spacer>
    <v-spacer></v-spacer>

    <v-btn href="https://github.com/sez543/sez543.github.io" icon>
      <i class="v-icon fa fa-github"></i>
    </v-btn>
  </v-app-bar>
</template>

<script>
export default {
  name: "Navigation",
  props: {
    is_locked: Boolean,
    score: Number,
  },
  created: function() {
    window.addEventListener("keydown", () => {
      this.start();
    });
  },
  methods: {
    start: function() {
      if (!this.is_locked) {
        this.$emit("start");
      }
    },
  },
};
</script>

<style lang="scss">
.v-app-bar {
  flex: none !important;
  width: 100% !important;
  height: auto !important;
}
.v-toolbar__content {
  height: auto !important;
}
img {
  width: 20px;
}
@media only screen and (max-width: 900px) {
  html {
    font-size: 15px !important;
  }
  .v-toolbar__content .v-btn.v-btn--icon.v-size--default,
  .v-toolbar__extension .v-btn.v-btn--icon.v-size--default {
    width: auto !important;
    height: auto !important;
  }
  .v-btn__content {
    padding: 5px !important;
  }
}
</style>
